export default {
  props: {
    product: {
      required: true
    }
  },
  computed: {
    firstVariant(){
      return this.product.variants[0]
    },
    firstPrintableArea(){
      let areas = _.keys(this.firstVariant.printable_area)
      return _.includes(areas, 'front') ? 'front' : _.head(areas)
    }
  }
}
